.intro-index {
  position: relative;
  width: 100%;
  min-height: 390px;
  background-image: url("../../img/bg-back-mobile@1x.jpg");
  background-color: $blue;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .intro-index {
      background-image: url("../img/bg-back-mobile@2x.jpg");
    }
  }

  @media ($tablet-width) {

    background-image: url("../img/bg-back-tablet@1x.jpg");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      .intro-index {
        background-image: url("../img/bg-back-tablet@2x.jpg");
      }
    }
  }

  @media ($desktop-width) {

    background-image: url("../img/bg-back-desktop@1x.jpg");

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      .intro-index {
        background-image: url("../img/bg-back-desktop@2x.jpg");
      }
    }
  }


  &::after {
    content: "";
    display: block;
    width: 100%;
    position: absolute;
    bottom: -1px;
    left: 0;
    height: 27px;
    background-image: url("../img/bg-intro-triangle-mobile.svg");
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: 322px 27px;
    background-position-x: -1px;

    @media ($tablet-width) {
      background-image: url("../img/bg-intro-triangle-tablet.svg");
      background-size: 100% 57px;
      height: 57px;
      bottom: 0;
    }

    @media ($desktop-width) {
      background-image: url("../img/bg-intro-triangle-desktop.svg");
      background-size: 1202px 57px;
      background-position-x: -1px;
    }
  }
}

.intro-index__image {
  padding-top: 90px;
  padding-bottom: 83px;
  display: block;
  margin: 0 auto;
  width: 282px;
  height: 214px;

  @media ($tablet-width) {
    width: 460px;
    height: 350px;
    padding-top: 80px;
    padding-bottom: 87px;
  }

  @media ($desktop-width) {
    padding-top: 78px;
    padding-bottom: 92px;
  }
}
