.form-sections {
  @media ($tablet-width) {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
  }
}


.form-sections__item {
  border-top: 1px solid #e5e5e5;
  padding: 18px 20px 20px;
  position: relative;

  @media ($tablet-width) {
    width: 100%;
    padding: 0;
    border: none;
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    margin-bottom: 72px;

    &:nth-child(3) {
      box-sizing: border-box;
      order: -1;
      width: 50%;
      border-right: 1px solid #e5e5e5;
      height: 296px;
      justify-content: space-between;
      flex-direction: column;
      align-items: flex-start;
    }

    &:nth-child(1) {
      width: 50%;
      flex-direction: column;
      align-items: flex-end;
      box-sizing: border-box;
      height: 296px;
      justify-content: space-between;
    }

    &:nth-child(2) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 50px;

      @media ($tablet-width) {
        margin-bottom: 45px;
      }

      @media ($desktop-width) {
        flex-direction: row;
        align-items: stretch;
      }
    }

    &:nth-child(4) {
      flex-direction: column;
    }

    &:nth-child(5) {
      flex-direction: column;
      margin-bottom: 27px;
    }
  }

  @media ($desktop-width) {
    margin-bottom: 68px;
  }
}
